<template>
	<div>
		<el-row>
			<el-col :span="24">
				<el-row>
					<el-col :span="6">
					<el-input
					  placeholder=""
					  icon="search"
					  v-model="selectedCompanyName"
					  :on-icon-click="searchTraditionalInfo">
					</el-input>	
					</el-col>
					
					
					
					<el-col :span="8">
					
					<el-date-picker
				      v-model="searchTime"
				      type="daterange"
				      placeholder="">
				    </el-date-picker>	
					</el-col>
					<el-button type="primary" @click="searchTraditionalInfo"></el-button>
					<el-button @click="passCheck"></el-button>
				    <el-button @click="failCheck"></el-button>
				</el-row>
				<el-row>
					<el-table
					:data="traditionalInfoData"
					highlight-current-row
					border
					@selection-change="handleRowChange">
					<el-table-column
					type="selection"
					width="65"
					align="center"
					></el-table-column>
					<el-table-column
					label=""
					prop="constructionEn.name"
					width="180px"
					align="center"
					>				
					</el-table-column>
					<el-table-column
					label=""
					prop="declareTime"
					width="177px"
					align="center">
					</el-table-column>	
					<el-table-column
					label=""
					prop="addNewConcrete"
					width="170px"
					align="center"
					></el-table-column>
					<el-table-column
					prop="addNewSteel"
					label=""
					width="170px"
					align="center"
					></el-table-column>
					<el-table-column
					prop="addNewTimber"
					label=""
					width="170px"
					align="center"
					></el-table-column>
					<el-table-column
					prop="checkedStatus.state"
					label=""	
					width="160px"
					align="center"
					></el-table-column>			
				</el-table>
				<el-pagination
					@current-change="handlePageNumChange"
					@size-change="handlePageSizeChange"
					:page-sizes="[5,10,15,20]"
				    :current-page="pageNum"
				    :page-size="pageSize"
				    layout="total, sizes, prev, pager, next, jumper"
				    :total="total">
				</el-pagination>
				</el-row>
			</el-col>
		</el-row>
		<msg-dialog ref="msgDialog"></msg-dialog>
	</div>
</template>
<script type="text/javascript">
import moment from 'moment'
import msgDialog from '../common/msgDialog.vue'
	export default{
		data:function(){
			return{
				traditionalInfoData:'',
				selectedCompanyName:'',
				selectedRow:[],
				selectedId:'',
				selectedConstructionId:'',
				pageNum:1,
				pageSize:10,
				total:'',
				searchTime:'',
			}
		},
		created:function(){
			this.getComponentCompanyInfoData()
		},
		methods:{
			getComponentCompanyInfoData:function(){
				var url=this.HOST+'/displayAllConstructionEnIndustrializationsBySubmit?page='+this.pageNum+'&rows='+this.pageSize
				this.$http.get(url).then(response=>{
					this.traditionalInfoData=response.data.rows
					this.total=response.data.total
				}).catch(response=>{
					this.$refs.msgDialog.notify("")
				})
			},
			handleRowChange:function(val){
				//alert("@@@@@@@"+JSON.stringify(val))
				if(val[1]){
					this.$refs.msgDialog.confirm("")
				}else{
					if(val[0]){	
						this.selectedId=val[0].id
						this.selectedRow=val[0]
						this.selectedConstructionId=val[0].constructionEn.id
					}else{
						this.selectedId=''
						this.selectedRow=''
						this.selectedConstructionId=''
					}
				}		
			},
			handlePageNumChange:function(val){
				this.pageNum=val
				this.getComponentCompanyInfoData()
			},
			handlePageSizeChange:function(val){
				this.pageSize=val
				this.getComponentCompanyInfoData()
			},
			searchTraditionalInfo:function(){
				if (this.selectedCompanyName==''||this.searchTime=='') {
					this.$refs.msgDialog.confirm("")
				}else{
					var startTime=moment(this.searchTime[0]).format("YYYY-MM-DD")
					var endTime=moment(this.searchTime[1]).format("YYYY-MM-DD")
					var url=this.HOST+'/queryConstructionEnIn?constructionEnName='+this.selectedCompanyName+'&startTime='+startTime+'&endTime='+endTime+'&page='+this.pageNum+'&rows='+this.pageSize
					this.$http.get(url).then(response=>{
						this.traditionalInfoData=response.data.rows
						this.total=response.data.total
					}).catch(response=>{
						this.$refs.msgDialog.notify("")
					})
				}
			},
			passCheck:function(){
				if (this.selectedId) {
					var url1 = this.HOST+'/checkConstructionEnIndustrialization?id='+this.selectedId+'&checkedStatusId='+1+'&constructionEnId='+this.selectedConstructionId			
					this.$http.post(url1).then(response=>{
						this.$refs.msgDialog.notify("")
						this.getComponentCompanyInfoData()
					}).catch(response=>{
						this.$refs.msgDialog.notify("")
					})
				}else{
					this.$refs.msgDialog.confirm("")
				}
				
			},
			failCheck:function(){
				if (this.selectedId) {
					var url = this.HOST+'/checkConstructionEnIndustrialization?id='+this.selectedId+'&checkedStatusId='+2+'&constructionEnId='+this.selectedConstructionId	
					this.$http.post(url).then(response=>{
						this.$refs.msgDialog.notify("")
						this.getComponentCompanyInfoData()
					}).catch(response=>{
						this.$refs.msgDialog.notify("")
					})
				}else{
					this.$refs.msgDialog.confirm("")
				}
				
			}
		},
		components:{
			msgDialog
		}
	}
</script>
<style type="text/css">
</style>


